}
.spinner[data-v-f86c277a] {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  position: relative;
  background: #bec8ff;
  border-radius: 50%;
  overflow: hidden;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.spinner[data-v-f86c277a]:before, .spinner[data-v-f86c277a]:after {
  content: '';
  position: absolute;
  top: 0;
  display: inline-block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.spinner[data-v-f86c277a]:before {
  -webkit-animation: circle-5-spinner-data-v-f86c277a 1s infinite;
          animation: circle-5-spinner-data-v-f86c277a 1s infinite;
  -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%;
  left: 0;
  background: #c7efcf;
}
.spinner[data-v-f86c277a]:after {
  animation: circle-5-spinner-data-v-f86c277a 1s 0.2s reverse infinite;
  -webkit-transform-origin: 100% 50%;
          transform-origin: 100% 50%;
  right: 0;
  background: #fff;
}
@-webkit-keyframes circle-5-spinner-data-v-f86c277a {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
50% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}
@keyframes circle-5-spinner-data-v-f86c277a {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
50% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}

